import React, { useEffect, useState, useRef } from "react";
import { string, arrayOf, func, bool } from "prop-types";

import AutoCompleteList from "../AutoCompleteList";

import styles from "./styles.module.scss";

const AutoCompleteInput = ({
  name,
  label,
  placeholder,
  options,
  disabled,
  fetch,
  setter,
}) => {
  const fieldRef = useRef();
  const [suggestionsVisibility, setSuggestionsVisibility] = useState(true);
  const [currentValue, setCurrentValue] = useState(null);
  const [suggestions, setSuggestions] = useState([]);

  const handleOnChange = (event) => {
    const { value } = event.target;

    fetch(value);
    setCurrentValue(value);
    setter(value);
  };

  const handleClick = (value) => {
    fieldRef.current.value = value;
    setSuggestionsVisibility(false);
    setCurrentValue(value);
    setter(value);
  };

  const handleOnFocus = () => {
    setSuggestionsVisibility(true);
  };

  const handleOnBlur = () => {
    setTimeout(() => {
      setSuggestionsVisibility(false);
    }, 100);
  };

  useEffect(() => {
    const filteredSuggestions = options.filter((option) =>
      option.includes(currentValue)
    );

    setSuggestions(filteredSuggestions);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentValue]);

  return (
    <div className={styles.wrapper}>
      <label htmlFor={`${name}-field`} className={styles.label}>
        {label}
      </label>
      <input
        type="text"
        onChange={handleOnChange}
        onFocus={handleOnFocus}
        onBlur={handleOnBlur}
        className={[styles.input, currentValue && styles.filled].join(" ")}
        id={`${name}-field`}
        data-testid={`${name}-field`}
        placeholder={placeholder}
        ref={fieldRef}
        disabled={disabled}
      />
      <AutoCompleteList
        suggestions={suggestions}
        handleClick={handleClick}
        filter={currentValue}
        visible={Boolean(suggestionsVisibility && currentValue)}
      />
    </div>
  );
};

AutoCompleteInput.propTypes = {
  label: string.isRequired,
  name: string.isRequired,
  placeholder: string.isRequired,
  options: arrayOf(string).isRequired,
  fetch: func.isRequired,
  setter: func.isRequired,
  disabled: bool,
};

AutoCompleteInput.defaultProps = {
  disabled: false,
};

export default AutoCompleteInput;
