import React, { useRef, useState } from "react";
import { Form } from "@unform/web";
import { useHistory } from "react-router-dom";
import { func } from "prop-types";

import SelectInput from "../SelectInput";
import SquareButton from "../SquareButton";
import AutoCompleteInput from "../../components/AutoCompleteInput";
import { Magnifier } from "../../libs/icons";
import { states } from "../../libs/data";

import useAutoComplete from "../../hooks/useAutoComplete";

import styles from "./styles.module.scss";

const SearchBlock = ({ successCallback }) => {
  const history = useHistory();
  const formRef = useRef();
  const [stateFilter, setStateFilter] = useState(null);
  const [keywordFilter, setKeywordFilter] = useState(null);
  const { fetch, data } = useAutoComplete();

  const handleSubmit = () => {
    const params = [];

    if (!stateFilter && !keywordFilter) {
      return;
    }

    if (stateFilter) {
      params.push(`estado=${stateFilter}`);
    }

    if (keywordFilter) {
      params.push(`termo=${keywordFilter}`);
    }

    successCallback();
    history.push(`/busca?${params.join("&")}`);
  };

  return (
    <section className={styles.wrapper}>
      <h2 className={styles.title}>
        Encontre a vaga <b>certa para você</b>
      </h2>
      <Form
        ref={formRef}
        onSubmit={handleSubmit}
        id="contact-form"
        className={styles.form}
      >
        <AutoCompleteInput
          placeholder="Exemplo: Analista Contábil"
          name="position"
          label="Pesquise um cargo ou palavra-chave"
          options={data}
          fetch={fetch}
          setter={setKeywordFilter}
        />
        <SelectInput
          name="location"
          label="Em qual estado?"
          placeholder="Selecione um estado"
          setter={setStateFilter}
          keys={{ label: "name", value: "acronym" }}
          options={[
            { name: "Todo o Brasil", acronym: "all" },
            ...states.brazil,
          ]}
        />
        <div className={styles.buttonWrapper}>
          <SquareButton
            text="Procurar vaga"
            type="submit"
            icon={<Magnifier color="#fff" />}
          />
        </div>
      </Form>
    </section>
  );
};

SearchBlock.propTypes = {
  successCallback: func.isRequired,
};

export default SearchBlock;
