import { useState } from "react";

import useAxios from "../useAxios";
import parseResults from "./parseResults";

const useAutoComplete = () => {
  const { exec, loading: useAxiosLoading } = useAxios();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const search = async (keyword) => {
    return await exec({
      useCredentials: true,
      method: "GET",
      url: `/busca?q=${keyword}`,
      // TODO: remove baseUrl option when back-end is implemented
      baseUrl: "https://run.mocky.io/v3/06c39fac-a769-465f-b3ab-96a576b25085",
    });
  };

  const fetch = async (keyword) => {
    if (loading || useAxiosLoading) return;

    setLoading(true);

    const result = await search(keyword);

    setData(parseResults(result.data));
    setLoading(false);
  };

  return { fetch, loading, data };
};

export default useAutoComplete;
