import React from "react";
import { arrayOf, shape, func, string } from "prop-types";

import { Chevron } from "../../libs/icons";

import styles from "./styles.module.scss";

const SelectInput = ({ setter, options, name, label, keys, placeholder }) => {
  const handleOnChange = (event) => {
    const { value } = event.target;
    setter(value);
  };

  return (
    <div className={styles.wrapper}>
      <label
        htmlFor={`${name}-field`}
        className={styles.label}
        data-testid={`${name}-label`}
      >
        {label}
      </label>
      <select
        onChange={handleOnChange}
        className={styles.field}
        id={`${name}-field`}
        defaultValue="null"
        data-testid={`${name}-field`}
      >
        <option
          disabled
          hidden
          value=""
          data-testid={`${name}-field-placeholder`}
        >
          {placeholder}
        </option>
        {options.map((option) => (
          <option
            key={`option_${option[keys.value]}`}
            value={option[keys.value]}
            data-testid={`${name}-field-option`}
          >
            {option[keys.label]}
          </option>
        ))}
      </select>
      <Chevron color="#8893ab" />
    </div>
  );
};

SelectInput.propTypes = {
  name: string.isRequired,
  label: string.isRequired,
  setter: func.isRequired,
  placeholder: string.isRequired,
  keys: shape({
    label: string.isRequired,
    value: string.isRequired,
  }).isRequired,
  options: arrayOf(shape().isRequired).isRequired,
};

export default SelectInput;
