import { states } from "../../../../libs/data";

export const convertAcronymToName = ({ acronym }) => {
  const match = states.brazil.filter((state) => state.acronym === acronym);

  if (!match.length) {
    console.error(`COULD NOT FIND A MATCH FOR STATE "${acronym}"`);
    return acronym;
  }

  return match[0].name;
};
